import React, { FC, useEffect } from 'react'
import './MethodPick.scss'
import List from 'ui/List/List'
import ListItem from 'ui/List/ListItem/ListItem'
import walletImg from 'images/Interface/wallet-03.svg'
import cardImg from 'images/Interface/card_circle_fill_32.svg'
import { useNavigate } from 'react-router-dom'
import WebApp from '@twa-dev/sdk'

const MethodPick: FC = () => {
  const navigate = useNavigate()

  WebApp.BackButton.onClick(() => {
    navigate(-1)
    WebApp.BackButton.hide()
  })
  useEffect(() => {
    WebApp.BackButton.show()
  }, [])
  
  return (
    <div className='method'>
      <h2 className="method__headline">How do you want to withdraw coins?</h2>
      <List>
        <ListItem
          icon_img={walletImg}
          mainText='To a crypto wallet'
          caption='No commission'
          buttonEvent={() => navigate('../crypto')}
        />
      </List>
      <List>
        <ListItem
          icon_img={cardImg}
          mainText='To a bank account'
          caption='1% – 2%'
          buttonEvent={() => navigate('../card')}
        />
      </List>
    </div>
  )
}

export default MethodPick